.section-header {
  padding: 54px 0;
  background-color: @lilak-color;
  position: relative;
  display: grid;

  @media (min-width: @tablet-width) {
    padding-top: 82px;
    padding-bottom: 65px;

    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 24px;
      bottom: 0;
      left: 0;
      background-image: url("../img/zigzag-line-white-tablet.svg");
      background-repeat: repeat-x;
    }
  }

  @media (min-width: @desktop-width) {
    padding-top: 82px;
    padding-bottom: 94px;

    &::after {
      height: 34px;
      left: 0;
      background-image: url("../img/zigzag-line-white-desktop-inner.svg");
    }
  }
}
